@import "~@/assets/styles/scss/_colors.scss";
        @import "~@/assets/styles/scss/_variables.scss";

.spotify-card {
  border: 1px dotted #000000;
  cursor: pointer;
  width: 100%;
  height: 100px;

  display: grid;
  grid-template-columns: 100px auto;
  border-radius: 0.9rem;

  transition: background-color 0.3s ease;

  &:hover {
    background-color: grey;
    border-radius: 0.9rem;
  }

  &__picture {
    width: 100%;
    height: 100%;
    overflow: hidden;

    img {
      display: block;
      height: 100%;
      object-fit: cover;
    }

    &--round img {
      clip-path: circle(40% at center);
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 10px 20px;
    overflow: hidden;

    span {
      // display: inline-block;
      width: calc(100%);
      text-align: left;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }

  &__title {
    font-weight: bold;
    font-size: 1.1rem;
    margin-bottom: 0.3rem;
  }

  &__subtitle {
    font-weight: 300;
    font-style: italic;
    font-size: 0.8rem;
    color: $color-spotify-light-grey;
  }
}
.feature-table{
  width:100%; 
  height: 100%;
  margin-top: 2rem;
  th{
    text-align: start;
  }  
  tr{
    line-height: 24px;
  }
  td{
    padding-left: 5px;
  }
}
.song-info-card{
  width: 100%; height: 100%; display:flex; align-items: center;
  gap: 1.5rem;
  margin-bottom: 3rem;

}
@media only screen and (min-width: map-get($breakpoints, 'lg')) {
  
   
  }
  @media only screen and (max-width: map-get($breakpoints, 'md')) {
   
    .song-info-card{
    flex-direction: column;
    }
    iframe{
