    }
  }
  @media only screen and (min-width: 0px) {
    .result-style{
    width: 90%;
   }
  }
  .animated-progress {
  width: 70%;
  height: 30px;
  border-radius: 5px;
  border: 1px solid rgb(189, 113, 113);
  position: relative;
}

.animated-progress span {
  height: 100%;
  display: block;
  width: 45%;
  color: rgb(255, 251, 251);
  line-height: 30px;
  position: absolute;
  text-align: end;
  padding-right: 5px;
}

.progress-white span {
  background-color: rgb(255, 251, 251);
}

