@import "~@/assets/styles/scss/_colors.scss";
        @import "~@/assets/styles/scss/_variables.scss";

.container {
  width: 100%;
  max-width: 800px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - var(--header-height));
      flex-direction: column;
  margin: 0 auto;
}

p {
  font-size: 32px;
  color: $color-white;
  margin-bottom: 1em;
}

.error {
  color: $color-error-red;
  font-size: 20px;
  font-weight: 300;
}
