@import "~@/assets/styles/scss/_colors.scss";
        @import "~@/assets/styles/scss/_variables.scss";

$border-radius: 20px;

.searchbox {
  --input-height: 40px;
  --search-icon-size: calc(var(--input-height) / 2);
  --clear-icon-size: calc(var(--input-height) / 3);
  --input-font-size: 18px;
  --input-padding: 0 40px;

  position: relative;
  width: 100%;
  max-width: 500px;
  height: var(--input-height);
  overflow: hidden;
  border-radius: $border-radius;

  &__clear-wrapper {
    position: absolute;
    width: calc(var(--input-height) * 0.75);
    height: var(--input-height);
    top: 0;
    right: calc(var(--input-height) * 0.2);
    border-radius: 0 $border-radius $border-radius 0;
  }

  &__icon {
    position: absolute;
    display: block;
    width: var(--search-icon-size);
    height: var(--search-icon-size);
    top: 50%;

    &--search {
      pointer-events: none;
      transform: translateY(-50%);
      left: calc(var(--search-icon-size) / 2);
    }

    &--clear {
      cursor: unset;
      width: var(--clear-icon-size);
      height: var(--clear-icon-size);
      transform: translateY(-50%);
      right: calc(var(--input-height) / 4);
    }
  }

  input {
    display: block;
    outline: none;
    box-shadow: none;
    text-shadow: none;
    width: 100%;
    height: 100%;
    border-radius: $border-radius;
    font-family: $font-family;
    font-size: var(--input-font-size);
    padding: var(--input-padding);
  }
}
